@import 'variables';

.about {
    min-width: 300px;
    margin: 0;
}

.name {
    font-size: 4rem;
    font-weight: 200;
    text-transform: uppercase;
    letter-spacing: .7rem;
    color: black;

}

.foto {
    min-width: 60vw;
    height: auto;
    object-fit: contain;
    opacity: 0.3;

}
.prim1 {
    position: absolute;
    top: 150px;
    left: 600px;
    margin: 30px 50px;
    background-color: none;
    border: none;
    filter: alpha(opacity=70); /*для IE8 и более ранних версий*/
    text-align: center;
}

.stack {
    font-size: 1rem;
    font-weight: 500;
    color: black;
}

.left {
    min-height: 70vh;
    display: flex;
    justify-content: left;
    align-items: center;
    flex-flow: row wrap;
}











.foto-contacts-skills {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  background-color: #d2d2d2;
  min-width: 300px;
}

.contacts a {
  text-decoration: none;
  color: #000000;
}

.contacts li {
  color: #777;
  list-style-type: none;
}

.contacts li:hover {
  color: #ffe;
  background-color: gray;
}

.block-skills {
  font-size: 0.55rem;
  font-weight: bold;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  min-width: 300px;
  padding: 5px;
}


.about-padding {
  padding: 20px;
}

.work-experience {
  font-size: 0.9rem;
  min-width: 300px;
  margin-top: 5px;
  background-color: #d2d2d2;
}

.work-experience-padding {
  padding: 10px 20px 5px 20px;
}

footer {
  font-size: 0.7rem;
}

