@import 'variables';

.cover {
    font-family: 'Nunito';
}

html, body {
    margin: 0;
    padding: 20px;
    height: 100%;
    min-height: 100%;
    font: 1.05rem/1.35 'Nunito', serif;
    background-color: $background-color;
    color: #4d4d4d;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

div .button {
    margin: 5px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
}

button, select {
    font-family: 'Nunito';
    font-size: 1.8em;
    text-transform: uppercase;
    letter-spacing: .7rem;
    border: none;
    margin: 10px 10px;
    padding: 10px 25px;
    border-radius: 9px;
    background: $background-color;
    transition: 0.3s;
  }

button:hover, select:hover {
    box-shadow: $box-shadow;
    transform: scale(1.05);
}

.active{
    font-weight: bold;
    color: rgb(0, 0, 0);
    text-decoration: none;
}

.normal {
    color: rgb(0, 0, 0);
    text-decoration: none;
}
